










































import {InspecIntakeModule} from '@/store/report_intake';
import {SnackbarModule} from '@/store/snackbar';
import {
  FileMetaData,
  getAllExecutions,
  getExecution,
  SplunkClient
} from '@/utilities/splunk_util';
import _ from 'lodash';
import Vue from 'vue';
import Component from 'vue-class-component';
import {Prop, Watch} from 'vue-property-decorator';
@Component({})
export default class FileList extends Vue {
  @Prop({type: Object, required: true}) readonly splunkClient!: SplunkClient;

  search = 'index="*" "meta.subtype"=header';
  loading = false;
  executions: FileMetaData[] = [];
  selectedExecutions: FileMetaData[] = [];

  /** Table info */
  headers = [
    {
      text: 'Filename',
      value: 'filename',
      filterable: true,
      align: 'start'
    },
    {
      text: 'Time',
      value: 'start_time'
    },
    {
      text: 'Action',
      value: 'action',
      sortable: false
    }
  ];

  @Watch('search')
  async onUpdateSearch() {
    this.mounted();
  }

  async mounted() {
    this.loading = true;
    this.executions = await getAllExecutions(
      this.splunkClient,
      this.search
    ).then((executions) => {
      this.loading = false;
      return executions;
    });
  }

  async loadResults() {
    this.loading = true;
    const files = this.selectedExecutions.map(async (execution) => {
      return getExecution(this.splunkClient, execution.guid).then(
        async (result) => {
          return InspecIntakeModule.loadText({
            text: JSON.stringify(result),
            filename: _.get(result, 'meta.filename')
          }).catch((err) => {
            SnackbarModule.failure(String(err));
          });
        }
      );
    });
    this.$emit('got-files', files);
  }

  logout() {
    this.$emit('signOut');
  }
}
